/*************************|   WORKSPACE FRAMEWORK   |
\*************************/
/* app.css has equivalents of these that are ID-specific instead of using classes. */
/* app.css has equivalents of these with classes named specifically for a CDD workspace, instead of a general class name. Replicated here. */
.column--list {
  border: 0.5em solid #f0f3f8;
  border-left: 1px solid #f0f3f8;
  border-right: none; }

.column--list-data {
  height: 33.5em; }

.column--list-counter {
  color: #cacaca;
  padding-right: 1em;
  text-align: right; }

.column--list-counter .icon {
  padding: 1em 0.25em 0 0.5em; }

.column--list-counter .total {
  font-size: 75%; }

.column--list-counter .total-selected {
  font-size: 75%; }

.column--list-counter .counter-selected {
  padding-left: 1em;
  text-align: left; }

.column--list.-is-collapsed-column {
  width: 2em !important; }

.column--list.-is-collapsed-column h6.panel--center-column-title-text.h7 {
  transform: rotate(90deg);
  transform-origin: top left;
  margin: 2em 0 0 0;
  width: 20em; }

.column--list.-is-collapsed-column .tree-button-container:last-child button {
  position: absolute;
  margin-top: 0.5em;
  margin-left: -1.5em; }

/* Styles for darkening the last tree/linked pane column */
.column--list.darker {
  background-color: #f0f3f8; }

.column--list.darker .column--list-counter {
  color: #8fa4bf; }

/* Allow Workspace to expand and fill full page height */
#page--workspace {
  height: auto;
  height: initial; }

/* Move and style the "Last edited by/datetime" display */
.workspace--controls .blurb {
  float: left;
  color: #8a8a8a;
  padding-left: 1em; }

/* Remove horizontal scroll bar in trees */
.workspace-tree {
  overflow-x: hidden; }

/* Remove dotted underline from expand/collapse tab content buttons */
.workspace--options button {
  border: none; }

/* Styling for in-tree buttons */
.node-action .button {
  height: 1.25em;
  width: 1.25em;
  text-align: center;
  padding: 0;
  margin: 0.25em; }

/* Styling for context menus on tree items */
.context-menu-list .context-menu-item .material-icons {
  margin-right: 0.5em;
  float: left; }

/* Styling for tab-lined panes */
.linked-pane-container {
  overflow-y: auto; }

.linked-pane-container .title {
  display: block;
  font-weight: bold;
  margin-bottom: 1em; }

.linked-pane-container .form--input-text [type="text"],
.linked-pane-container .form--input-text [type="password"],
.linked-pane-container .form--input-text [type="date"],
.linked-pane-container .form--input-text [type="datetime"],
.linked-pane-container .form--input-text [type="datetime-local"],
.linked-pane-container .form--input-text [type="month"],
.linked-pane-container .form--input-text [type="week"],
.linked-pane-container .form--input-text [type="email"],
.linked-pane-container .form--input-text [type="number"],
.linked-pane-container .form--input-text [type="search"],
.linked-pane-container .form--input-text [type="tel"],
.linked-pane-container .form--input-text [type="time"],
.linked-pane-container .form--input-text [type="url"],
.linked-pane-container .form--input-text [type="color"],
.linked-pane-container .form--input-text textarea {
  background: transparent; }

