/**************************|   PAGE-SPECIFIC TWEAKS   |
\**************************/
/*** Project Home - Profile tab ***/
/* Adjust tab widths to accommodate an extra tab  */
#page--directory .dashboard---panel-project .feature--tabs-list li {
  width: 16.66%; }

/* Tweak margins for tab contents */
#page--directory .feature--edit-mode {
  margin-top: -0.5em; }

/* Hide required stars when tab is not in edit mode */
.-edit-mode-off .form--input-text.-is-required label:after {
  color: transparent; }

/* Remove dropdown arrow when tab is not in edit mode */
.-edit-mode-off select:disabled {
  background-image: none; }

/* Remove dropdown hover effect when tab is not in edit mode */
.-edit-mode-off select:disabled:hover {
  border: 1px solid transparent !important; }

/* Remove background from textarea when tab is not in edit mode */
.-edit-mode-off textarea:disabled {
  background-color: transparent; }

/* Fix tab content border when in edit mode */
.-edit-mode-on .tabs-content {
  border-right: 3px solid #627bbe; }

/*** Project Home - Users tab ***/
/* User Role dropdown fixes */
.data--table td select {
  font-size: 0.85em;
  /* Same text size as the rest of the table */
  background: transparent;
  /* Don't interfere with zebra striping */
  margin: 0;
  /* Center inside table rows */ }

/* Center User Role dropdowns inside table rows */
.data--table td .form--input-select {
  margin-bottom: 0; }

/* Increase spacing between table and search field */
#userSearchCell {
  padding-top: 1.5em; }

/*** Project Home - TDD & Publish tabs ***/
/* Fix for TDD Snapshots and Publish History being too narrow */
#page--directory .dashboard---panel-project .feature--tabs-list .tabs-content li {
  width: auto; }

/*** Project Updates panel ***/
/* Updates by someone other than the current user should be near-white */
.panel--update .panel--update-item .panel--tooltip,
.panel--update .panel--update-item .panel--tooltip :hover {
  color: #f0f3f8; }

/* Updates by current user should be dimmer */
.panel--update .panel--update-item.-is-read .panel--tooltip,
.panel--update .panel--update-item.-is-read .panel--tooltip :hover {
  color: #627bbe; }

/* Tweak update object-kind icon */
.panel--update .panel--update-item .panel--tooltip i {
  text-decoration: none;
  vertical-align: text-bottom; }

/*** Units of Measure Workspace ***/
/* Tweak alignment of Yes/No switches REMOVED!*/
/*** Reports ***/
/* Move multi-tab report subtitles down a bit */
.sub-report-title h3 {
  margin-top: 1em; }

/* Reduce gap between header line and report table (or tabs for multi-tab report) */
#page--reports #excelDownload .button {
  margin-bottom: 1em; }

/*** Device Maintenance workspace ***/
/* Align all table contents to top so that different VS inclusion switches always line up */
.device-maintenance-table-body tr {
  vertical-align: top; }

/* Reduce gap between VS inclusion switch and expiration display */
.device-maintenance-table-body .switch {
  margin-bottom: 0; }

/* Reduce gap between top of cell and VS inclusion switch, and line up left edges of inclusion switch and expiration display */
.device-maintenance-table-body .switch-paddle {
  margin-top: 0;
  margin-left: 0; }

/* Make sure expiration date doesn't wrap */
.device-maintenance-table-body .expiration-date-view-container,
.device-maintenance-table-body .expiration-date-edit-container {
  white-space: nowrap; }

