/* Workspace tree filter field: should be gray, not bright green */
.feature--filter-input:-ms-input-placeholder {
  color: #cacaca !important; }

/* Workspace tree filter field: remove IE's clear button in favor of our own */
.feature--filter-input::-ms-clear {
  display: none; }

