/*************************|   GLOBAL FIXES/TWEAKS   |
\*************************/
/* Fixes for opening panels moving buttons weirdly */
#content--page.is-open-right {
  position: static;
  margin-left: 250px; }

#content--page.is-open-left {
  margin-right: 250px; }

/* Smoothly open panels */
.off-canvas-content {
  transition: transform 0.5s ease, margin 0.5s ease; }

/* Progress bar: hide when not in use */
.content--progress {
  background-color: transparent; }

/* Progress bar: show when in use */
.content--progress.is-active {
  background-color: #f0f3f8; }

/* Progress bar: Move percentage to stay at the right of the bar */
.content--progress .progress-bar .progress-bar-number {
  padding-right: 1em;
  font-size: 80%;
  width: 100%;
  text-align: right; }

/* Progress bar: When at 0%, move text to center of bar */
.content--progress .progress-bar-no-number {
  text-align: center;
  position: absolute;
  width: 100%; }

/* Progress bar: create some separation between page content and bar */
.content--page {
  padding: 0 0 0.5em 0; }

/* Progress bar: reduce separation between bar and bottom of page to match the above */
.content--progress {
  margin: 0 0 0.5em 0; }

/* Mitigate visual issue with header/footer when resizing tables (such as when opening/closing panels) */
.dataTable-dynamic-size-container .dataTables_scrollHead {
  background: #f0f3f8; }

.dataTable-dynamic-size-container .dataTables_scrollFoot {
  background: #f2f2f2; }

/* Checkbox fixes */
.form--input-checkbox input[type="checkbox"] + label:before {
  margin-top: -0.15em;
  /* Align checkbox with text */
  content: "check_box_outline_blank";
  /* Fix weird whitespace issue in app.css */ }

/* Radio button fixes */
.form--input-radio input[type="radio"] + label:before {
  margin-top: -0.15em;
  /* Align radio button with text */ }

/* Fix for the Project Search panel having horizontal scrolling */
.off-canvas .form--input-checkbox input + label {
  width: auto;
  margin-right: 0; }

/* All tab contents need a bit of padding */
.tabs-content {
  padding: 1em;
  height: 544px; }

/* Don't use fixed tab content size */
.content--tab {
  height: 433px; }

/* Allow using .taller for textareas throughout the site */
/* Fix close buttons in popups */
.reveal .close-button {
  height: 1em;
  width: 1em;
  padding: 0 0 0 0;
  border: none; }

/* Utility classes to allow overriding toggle switch alignment */
.left-switch-container .switch {
  text-align: left; }

.center-switch-container .switch {
  text-align: center; }

/* Making the label for a checked checkbox bolder actually changes the sizing,
    which can wind up breaking the scrollbars in IE  */
.dataTable-dynamic-size-container th .form--input-checkbox input[type="checkbox"]:checked + label {
  font-weight: normal; }

/*
    Ensures that when we're using a checkbox in the header of a DataTables-enabled table
    that's also using the Fixed Headers extension, the custom icon doesn't show up duplicated
    and floating weirdly in the table body.
*/
.dataTables_scrollBody th .form--input-checkbox input[type="checkbox"] + label:before {
  content: ""; }

/* Fix jQuery UI elements to use our colors */
.ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active,
a.ui-button:active,
.ui-button:active,
.ui-button.ui-state-active:hover {
  border: 1px solid #002b59;
  background: #002b59;
  font-weight: normal;
  color: #ffffff; }

/* When using an autocomplete inside a popup, make sure the autocomplete dropdown is visible */
.reveal-overlay ~ .ui-autocomplete {
  z-index: 1006; }

/* Remove lock icon from disabled switches */
.switch.switch-disable input[disabled] + label:before,
.switch.switch-disable input[readonly] + label:before {
  content: ""; }

/* Make sure cursor still shows up near a locked switch. On top of the switch, other styles set it to not-allowed. */
.switch.switch-disable {
  cursor: auto; }

