.form--input-checkbox input[type="checkbox"] + label:before,
.form--input-checkbox input[type="checkbox"]:checked + label:before,
[type='text']:focus:focus:after,
[type='password']:focus:focus:after,
[type='date']:focus:focus:after,
[type='datetime']:focus:focus:after,
[type='datetime-local']:focus:focus:after,
[type='month']:focus:focus:after,
[type='week']:focus:focus:after,
[type='email']:focus:focus:after,
[type='number']:focus:focus:after,
[type='search']:focus:focus:after,
[type='tel']:focus:focus:after,
[type='time']:focus:focus:after,
[type='url']:focus:focus:after,
[type='color']:focus:focus:after,
textarea:focus:focus:after,
input[disabled] + label:before,
